.profileContener {
  background-color: rgb(0, 0, 0);
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 80px;
  color: white;
  border-radius: 3px;
}
.profileContener > h1 {
  padding: 20px;
  font-size: 2rem;
  font-weight: 500;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.lhContener {
  height: 100%;
  width: 30%;
}
.lhContener > img {
  max-width: 80px;
  padding-left: 40px;
  padding-top: 10px;
}
.secondRowContener {
  display: flex;
  justify-content: space-between;
}

.rhContener {
  width: 70%;
}
.rhContener > h3 {
  padding-left: 20px;
  padding-top: 40px;
  font-size: 1.5rem;
  font-weight: 600;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.rhContener > button {
  padding: 6px;
  margin-left: 10px;
  margin-top: 40px;
  width: 90%;
  margin-bottom: 30px;
  border-radius: 2px;
  cursor: pointer;
  border: none;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.1rem;
  background-color: #e50914;
  color: white;
}
.Profile {
  background-color: rgb(20, 20, 20);
  height: 100vh;
}
