.backDrop {
  background-position: center;
  background-size: cover;
  color: white;
  object-fit: contain;
  height: 448px;
}
.bannerContents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.button {
  color: white;
  outline: none;
  font-weight: 700;
  margin-right: 1rem;
  border-radius: 5px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
}
.movieDescription {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.9rem;
  height: 80px;
  max-width: 360px;
}
.button:hover {
  color: black;
  cursor: pointer;
  transition: all 0.2s;
  background-color: #e6e6e6;
}
.faidbottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgb(37, 37, 37, 0.61),
    #111
  );
}
