.SignPage {
  height: 600px;
  width: 100%;
}
.SignInPage {
  width: 100%;
  height: 100%;
  color: white;
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
  background-color: rgba(0, 0, 0, 0.4);
}
.navBar {
  display: flex;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  padding: 20px;
  height: 30px;
}
.netflixLogo {
  position: fixed;
  left: 40px;
  top: 25px;
  width: 120px;
  cursor: pointer;
}

.sigInButton {
  position: fixed;
  right: 40px;
  top: 25px;
  background-color: #e50914;
  color: white;
  cursor: pointer;
  border: none;
  font-weight: 400;
  border-radius: 3px;
  font-size: 1rem;
  padding: 8px 22px;
}
.home_Page {
  top: 30%;
  width: 100%;
  position: absolute;
  left: auto;
  right: auto;
  text-align: center;
  z-index: 1;
}
.h3text {
  max-width: none;
  padding-bottom: 20px;
  font-size: 1.2rem;
  margin: 0 auto;
  font-weight: 400;
  padding: 0 5%;
  margin-bottom: 8px;
}
.h2text {
  font-weight: 400;
  margin-bottom: 30px;
  font-size: 1.625;
}
.h1text {
  font-size: 3.125rem;
  margin-bottom: 20px;
  line-height: 1.1;
  direction: ltr;
  font-family: "Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.emailForm {
  margin-left: auto;
  margin-right: auto;
}
.emailForm > input {
  font-size: 1.5rem;
  font-family: "Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  padding: 12px;
  width: 400px;
  margin-bottom: 10px;
}
.emailForm > button {
  font-family: "Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background-color: #e50914;
  padding: 12px;
  border: none;
  counter-reset: 5px;
  color: white;
  font-size: 1.5rem;
  width: 200px;
}
