.BackDropClass {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.ImageForLogo {
  height: 45px;
  width: 167px;
  vertical-align: middle;
  padding: 20px;
}
.SignupContener {
  max-width: 300px;
  padding: 70px;
  margin-left: auto;
  margin-right: auto;
  color: white;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.85);
}
.SignupContener > form {
  display: grid;

  flex-direction: column;
}
.SignupContener > form > h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.SignupContener > form > h3 {
  font-size: 0.8rem;
  margin-bottom: 20px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.SignupContener > form > input {
  color: white;
  border-radius: 4px;
  border: 0;
  position: relative;
  padding: 10px;
  margin-bottom: 10px;
  background: #333;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
  direction: ltr;
}
.SignupContener > form > button {
  border-radius: 4px;
  background: #e50914;
  vertical-align: middle;
  letter-spacing: 0.1px;
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  position: relative;
  font-weight: 700;
  padding: 12px;
  width: 100%;

  border: none;
  margin: 24px 0 12px;
}
