.footer {
  width: 100%;
  color: rgb(128, 128, 128);
  background-color: black;
  margin-top: 7px;
}
.footerText {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  display: flex;
}

.footerText > ul > li {
  font-size: 0.9rem;
  padding-bottom: 20px;
  text-align: initial;
  cursor: pointer;
}
.footerText > ul {
  width: 25%;
  list-style-type: none;
}
.footer > h1 {
  font-size: 1.2rem;
  padding-top: 50px;
  margin-left: 18%;

  cursor: pointer;
}
.footer > h2 {
  font-size: 1.1rem;
  padding-top: 50px;
  margin-left: 15%;
  padding: 40px;
}
