.posterRow {
  display: flex;
  background-color: black;
  justify-content: space-between;
  margin-top: 7px;
  width: 100%;
  color: white;
}
.posterRowText {
  width: 52%;
}
.posterRowText > h1 {
  text-align: initial;
  font-size: 3rem;
  margin-top: 100px;
  margin-left: 60px;
  margin-right: 60px;
  font-family: "Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.posterRowText > h2 {
  font-family: "Netflix Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-align: initial;
  font-size: 1.8rem;
  margin-left: 60px;
  margin-right: 60px;
  font-weight: 400;
}
.posterRowImage {
  width: 48%;
}
.posterRowImage > img {
  height: 380px;
  left: 45px;
  padding: 35px;
}
