.navLogo {
  position: fixed;
  left: 25px;
  width: 90px;
  cursor: pointer;
}
.avathar {
  position: fixed;
  right: 25px;
  width: 40px;
  cursor: pointer;
}

.transparentNavbar {
  position: fixed;
  display: flex;
  justify-content: space-between;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 30px;
  background-color: #111;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.blackNav {
  position: fixed;
  display: flex;
  justify-content: space-between;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 30px;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}
