.row {
  margin-left: 20px;
  color: white;
}

.poster {
  max-height: 250px;
  margin-right: 10px;
  cursor: pointer;
  transform: translate(450ms);
}
.posters {
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.posters::-webkit-scrollbar {
  display: none;
}
.poster:hover {
  transform: scale(1.09);
}
.smallposter {
  max-height: 150px;
  margin-right: 10px;
  cursor: pointer;
  transform: translate(450ms);
}
.smallposter:hover {
  transform: scale(1.09);
}
